.nav.justify-content-evenly {
  background-color: #2bbbad;
}
.fa.fa-shopping-cart {
  font-size: 1.5rem;
  color: aliceblue;
}

.cart-count {
  position: absolute;
  top: 4px;
  right: 10px;
  background: #0070ff;
  font-size: 10px;
  border-radius: 50%;
  color: #fff;
  font-style: normal;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 15px;
  width: 15px;
  font-weight: bold;
}

.price-class {
  color: white;
  font-weight: bold;
}
