.mask.rgba-white-slight {
  width: 340px !important;
  height: 340px !important;
}

.modal-btn {
  display: flex;
}
.modal-btn-btn {
  border-radius: 4rem;
  border: 1px solid #2bbbad;
  background-color: #2bbbad;
}

.btn-info.btn.Ripple-parent {
  background-color: #2bbbad !important;
}
.btn-secondary.btn.Ripple-parent {
  background-color: #2bbbad !important;
}
.btn-secondary.btn.Ripple-parent:hover {
  background-color: #2bbbad !important;
}
.fas.icons-btn-color {
  color: #fffffe !important;
}
.fa.fa-shopping-cart {
  font-size: 2rem !important;
  color: #2bbbad;
}

.cart-count {
  position: absolute;
  top: 3rem;
  right: 15.7rem;
  background: #0070ff;
  font-size: 10px;
  border-radius: 50%;
  color: #fff;
  font-style: normal;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 15px;
  width: 15px;
  font-weight: bold;
}
